


















































































	import uniIcon from "@/components/uni-icon/uni-icon.vue"
	import uniCollapse from '@/components/uni-collapse/uni-collapse.vue'
	import uniCollapseItem from '@/components/uni-collapse-item/uni-collapse-item.vue'
	import uniList from '@/components/uni-list/uni-list.vue'
	import uniListItem from '@/components/uni-list-item/uni-list-item.vue'
	import uniPopup from '@/components/uni-popup/uni-popup.vue'
	export default {
		components: {uniIcon,uniCollapse,uniCollapseItem,uniList,uniListItem,uniPopup},
		data(){
			return{
				bottom:false,
				activity:false,
				bgMoney:"", //定义对应的充值按钮的背景色
				/* moneyInfo:"", //输入的整数金额 */
				checkNum:-1,
				cheng: '',
				items:[{
					value:'red',
					name:'500',
					typeId:'-1'
				},
				{
					value:'red',
					name:'1000',
					typeId:'0'
				},
				{
					value:'red',
					name:'2000'
				},
				{
					value:'red',
					name:'5000'
				},
				{
					value:'red',
					name:'10000'
				}],
				balance:"", //当前余额
				Signboard:'', //token令牌
				Signguid:'', //用户标识
				rueles:"", //优惠活动的数组
				ruelesNum:0, //活动信息选择的下标
				ruelesMsg:"查看优惠活动", //活动信息选中后的显示
				pmtMethod:"微信", //充值方式显示
				pmtNum:"1", //充值规则序号  1-微信 2-支付宝
				bgWeChat:"", //微信选中后的颜色
				bgAlipay:"" ,//支付宝选中的颜色
				weChatColor:"", //微信选中后的字体颜色
				alipayColor:"", //支付宝选中后的字体颜色
				iconWeChat:"#C3C3C3", //微信选中后的图标颜色
				iconAlipay:"#C3C3C3", //支付宝选中后的图标颜色
				sltMoney:"0.00", //用于存用户选择的充值金额
				rId:"", //充值规则序号
				timeId:"" ,//用来存取生成的Id
				ruelesInfo: '',
				actualMoney:'0.00',
				paymentText:"",
				msg:""
			}
		},
		methods:{
			numInput(e){
				console.log(e)
				this.checkNum = -2;
				// var vel = e.target.value
				// console.log(vel)
			},
			zfClick(e){ 
				this.bottom = true;
			},
			hidePopup(evt){
				this.bottom = false;
			},
			money:function(evt){
				this.cheng = evt.target.value;
				this.sltMoney=evt.target.value;
				this.discount();
			},
			hdClick(e){ //点击优惠活动触发的事件：会弹出优惠活动内容
				this.activity=true;
				console.log(this.ruelesMsg);
			},
			activityPopup(e){
				this.activity=false;
			},
			rhgClick(index,name){
				this.cheng = '';
				this.sltMoney=name;
				console.log("选择充值金额为:"+this.sltMoney)
				this.checkNum = index;
				this.discount();
			},
			discount() {
				let rleArray=[];
				//判断用户充值的金额是否满足活动满减的条件
				for(let i=0;i<this.rueles.length;i++){
					if(parseFloat(this.sltMoney) >= parseFloat(this.rueles[i].recharge)){
						rleArray.push(this.rueles[i]);
						
					}
				}
				this.ruelesInfo=rleArray;
				if(this.ruelesInfo.length==0){
					this.actualMoney=this.sltMoney;
				}else{
					this.actualMoney=parseFloat(this.sltMoney)+parseFloat(this.ruelesInfo[this.ruelesNum].send);
					this.ruelesMsg = '满'+ this.ruelesInfo[this.ruelesNum].recharge +'送'+ this.ruelesInfo[this.ruelesNum].send
				}
			},
			ruelesClick(evt){
				console.log(evt)
				console.log("充值规则序号:"+this.ruelesInfo[evt].r_id);
				this.rId=this.ruelesInfo[evt].r_id;
				console.log("充值规则序号2:"+this.rId);
				this.ruelesNum=evt;
				this.ruelesMsg='满'+this.ruelesInfo[evt].recharge+'送'+this.ruelesInfo[evt].send;
				this.actualMoney=parseFloat(this.sltMoney)+parseFloat(this.ruelesInfo[evt].send);
			},
			ruelesYes(evt){ //优惠活动选择后的确定按钮触发的事件
				this.activity=false;
			},
			pmtClick(evt,recharges){
				console.log(evt);
				this.pmtNum=evt;
				this.pmtMethod=recharges.name;
			},
			weChatClick(evt){ //点击支付方式中的 “微信触发的事件”
				console.log(evt);
				this.bgWeChat="red";
				this.weChatColor="#fff";
				this.iconWeChat="#fff";
				this.bgAlipay="";
				this.alipayColor="";
				this.iconAlipay="#C3C3C3";
				this.pmtMethod="微信";
				this.pmtNum=evt;
				console.log("pmtNum:"+this.pmtNum);
			},
			alipayClick(evt){ //点击支付方式中的 “支付宝触发的事件”
				console.log(evt);
				this.bgAlipay="red";
				this.alipayColor="#fff";
				this.iconAlipay="#fff";
				this.bgWeChat="";
				this.weChatColor="";
				this.iconWeChat="#C3C3C3";
				this.pmtMethod="支付宝";
				this.pmtNum=evt;
				console.log("pmtNum:"+this.pmtNum);
			},
			paymentYes(evt){ //点击支付方式弹出框中的确定触发的事件
				this.bottom=false;
			},
			btnRecharge(evt){ //点击充值按钮触发的事件
				uni.request({
					/* url:'http://192.168.199.194/mobile/Alipay/APl', */
					url:'http://192.168.199.194//mobile/recharge/Wapay',
					method:'GET',
					data:{
 						Ident_Signboard: this.Signboard,
 						Ident_Signguid: this.Signguid,
						/* total:this.sltMoney, */
						moneys:this.sltMoney,
 						zhifu:this.pmtNum,
 						r_id:this.rId
					},
					success: (res) => {
						console.log(res)
						if(this.pmtNum==1){
							let routeData = this.$router.resolve({
							   name: "searchGoods",
							   query: params,
							   params:{catId:params.catId}
							});
							window.open(routeData.href, '_blank');
						}else{
							const div = document.createElement('div')
							div.innerHTML = res.data //此处form就是后台返回接收到的数据
							document.body.appendChild(div);
							document.forms[0].submit();
						}
					}
				})
			},
			paymentTime(res){
				if(this.pmtNum==1){
					uni.request({
						url:'http://192.168.199.194/mobile/wechat/QueryRep',
						method:'GET',
						data:{
							Ident_Signboard: this.Signboard,
							Ident_Signguid: this.Signguid,
							orderNum:res.data.orderNum,
							tag:res.data.tag
						},
						success: (res) => {
							if(res.data.status==0){
								uni.showToast({
									title:'充值成功',
									icon:'none'
								})
								clearInterval(this.timeId);
							}
						}
					})
				}else{
					uni.request({
						url:'http://192.168.199.194/mobile/Alipay/RechargeQuery',
						method:'GET',
						data:{
							Ident_Signboard: this.Signboard,
							Ident_Signguid: this.Signguid,
						},
						success: (res) => {
							if(res.data.status==0){
								uni.showToast({
									title:'充值成功',
									icon:'none'
								})
								clearInterval(this.timeId);
							}
						}
					})
				}
			}
		},
		onLoad:function() {
			var jsonList=uni.getStorageSync("jsonList");
			var data = JSON.parse(jsonList); //JSON字符串转对象
			this.Signboard = data.Ident_Signboard;
			this.Signguid = data.Ident_Signguid;
			//获取当前账户余额
			uni.request({
				url:'http://192.168.199.194/mobile/recharge/getRecharge',  //获取当前账户余额的接口地址
				method:'GET',
				data:{
					Ident_Signboard: this.Signboard,
					Ident_Signguid: this.Signguid
				},
				success: (res) => {
					this.balance=res.data.balance.account; //获取当前余额
					if(res.data.status==0){
						uni.showToast({
							title:'当前余额获取成功',
							icon:'none'
						})
					}else{
						uni.showToast({
							title:'获取当余额失败',
							icon:'none'
						})
					}
				}
			}),
			uni.request({
				url:'http://192.168.199.194/mobile/recharge/rueles',
				method:'GET',
				data:{
					Ident_Signboard: this.Signboard,
					Ident_Signguid: this.Signguid
				},
				success: (res) => {
					console.log(res);
					console.log(res.data.data);
					this.rueles=res.data.data;
					this.ruelesInfo=res.data.data;
					if(res.data.status==0){
						uni.showToast({
							title:'活动信息获取成功',
							icon:'none'
						})
					}else{
						uni.showToast({
							title:'活动信息获取失败',
							icon:'none'
						})
					}
				}
			})
		}
	}
	
